import { Injectable } from '@angular/core';
import { APIDispatch, APIFactory, UpdateTimeListAPI, UnbindDeviceAPI, DeviceService } from '@allianzSND/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SettingComponentService {

  constructor(private dispatcher: APIDispatch,
    private APIFactory: APIFactory,
    private deviceService: DeviceService
  ) { }

  getUpdateTimeList(): Observable<any> {

    console.debug('getUpdateTimeList');

    let updateTimeListAPI: UpdateTimeListAPI = <UpdateTimeListAPI>this.APIFactory.getAPI('getUpdateTimeList');
    return Observable.create((observer) => {
      this.dispatcher.dispatch(updateTimeListAPI).subscribe((data) => {
        if (data['Header'].status) {

          let returnData: Array<any> = [];
          let orders = ["Homepage", "Goal_Setting", "Customer", "Calendar", "Progress"];
          orders.forEach(x => {
            returnData.push({
              MainFunc: data['Body'].filter(y => y.MainFunc == x)[0].MainFunc,
              BackendTime: data['Body'].filter(y => y.MainFunc == x)[0].BackendTime
            })
          });
          observer.next(returnData);
          observer.complete();
        } else {
          observer.next([]);
          observer.complete();
        }
      });
    });
  }

  public async unbindDevice(): Promise<any> {
    let unbindDeviceAPI = <UnbindDeviceAPI>this.APIFactory.getAPI('unbindDevice');
    unbindDeviceAPI.setDeviceID(this.deviceService.getDeviceUUID());
    console.warn("this._getDeviceUUID(): ", this.deviceService.getDeviceUUID());
    return this.dispatcher.dispatch(unbindDeviceAPI).toPromise();
  }
}
